.App {
  text-align: center;
}

.App-header {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(20px + 4vmin);
  color: white;
}

.Page-header {
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: palevioletred;
}
.h5 {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  color:aquamarine
}

.pill {
  background-color: #282c34;
  font-weight: bold;
}

.input {
  flex-direction: column;
  align-items: left;
  justify-content: left;
  color: palevioletred;
}

.btn {
  background-color: aquamarine;
  font-weight: bold;
  color: #282c34;
}

.btn:hover {
  color:palevioletred;
  font-size: 20px;
}

body {
  background-color:#282c34
}

label {
  color:aquamarine
}



